<template>
  <div class="post">
      <div class="postTitle">
    <div class="profile">
      <div
        class="profileImg"
        :style="{ backgroundImage: `url(${data.userImage})` }"
      ></div>
      <div class="profimeText">
        <p class="userID">{{ data.name }}</p>
        <p class="date">{{ data.date }}</p>
      </div>
    </div>
    <a href=""><img src="../assets/img/follow.png" alt="" /></a>
  </div>
  <div class="postContent">
    <a :href="data.link">
      <img :src="data.postImage" alt="" />
      <h3 class="postName">{{ data.title }}</h3>
    </a>
  </div>
  <div class="postInfo">
    <ul class="left">
      <li>
        <img src="../assets/img/view.png" alt="" />
        <p>{{data.view}}</p>
      </li>
      <li>
        <img src="../assets/img/comment.png" alt="" />
        <p>{{data.comment}}</p>
      </li>
      <li>
        <img src="../assets/img/share.png" alt="" />
        <p>{{data.share}}</p>
      </li>
    </ul>
    <ul class="right">
      <li>
        <button class="like" :class="{'active': isLike}" @click="likeBtn"></button>
        <p>{{myLike}}</p>
      </li>
    </ul>
  </div>
  </div>
</template>

<script>

export default {
  name: "post",
  props: {
    data: Object,
  },
  computed:{

  },
  data(){
      return{
          myLike: this.data.likes,
          isLike : false,
      }
  },
  methods:{
      likeBtn(){
          if(this.isLike == true){
              this.myLike--;
              this.isLike = false;
          }
          else{
              this.myLike++;
              this.isLike = true;
          }
      }
  }
};
</script>

